.icon {

  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    text-transform: uppercase;
    color: #344f10;
    height: 40px;
    width: 40px;
  
  
    @include onDesktop {
      display: inline;
    }
  
    &-block {
      display: flex;
    }
  }
  
  &__img {
    height: 24px;
    width: 24px;
  }
  
  &__img-logo {
    @include onDesktop {
      height: 50px;
      width: 50px;
      transition: 0.1s;
  
      &:hover {
        transform: scale(1.01);
      }
    }
  }
  
  &__img-shop {
    @include onDesktop {
      height: 26px;
  
      transition: 0.2s;
  
      &:hover {
        transform: scale(1.15);
      }
    }
  }
  
  &__text {
    line-height: 50px;
    margin: 0 12px;
    font-size: 14px;
    letter-spacing: 0.2px;
  }
  
  &__close {
    height: 24px;
    margin-top: 16px;
  
    @include onIPad {
      margin-top: 24px;
    }
  }
  
  &__footer {
    text-decoration: none;
  
    &-img {
      width: 20px;
      height: 20px;
  
      @include onDesktop {
        transition: 0.1s;
  
        &:hover {
          transform: scale(1.05);
        }
      }
    }
  }
  
  &__footer-instagram {
    margin-left: 12px;
  }
  
}
