.shop {
  margin-top: 60px;
  
  @include onIPad {
    margin-top: 80px;
  }
  
  @include onDesktop {
    margin-top: 180px;
  
  }
  
  &__content {
    @include onDesktop {
      padding-left: 100px;
    }
  }
  
  &__title {
    text-align: center;
  
    @include onIPad {
      text-align: left;
    }
  }
  
  &__menu {
    margin-top: 10px;
  
    @include onDesktop {
      margin-top: 20px;
    }
  
    &__link {
      position: relative;
      text-decoration: none;
      font-size: 14px;
      line-height: 21px;
      color: #828282;
      text-transform: uppercase;
  
      @include onDesktop {
        font-size: 18px;
        line-height: 27px;
  
        letter-spacing: 1px;
      }
  
      transition: 0.2s;
  
      &:hover {
        color: #333;
        &::after {
          position: absolute;
          content: "";
          border: 1px solid #333;
          width: 100%;
        }
      }
    }
  }
  
  &__list {
    display: flex;
    justify-content: space-between;
    max-width: 280px;
  
    @include onDesktop {
      max-width: 400px;
    }
  }
  
  &__item {
    position: relative;
    transition: 0.2s;
    height: 40px;
  }
  
  &--is-active {
    color: #333;
    position: relative;
  
    &::after {
      position: absolute;
      content: "";
      border-bottom: 2px solid #333;
      width: 100%;
    }
  }
  
  &__cards {
  
    margin-top: 20px;
  
    & > :nth-child(n + 2) {
      display: none;
    }
  
    @include onIPad {
      display: flex;
      & > :nth-child(2) {
        display: block;
        margin-left: 30px;
      }
      & > :nth-child(n + 3) {
        display: none;
      }
    }
  
    @include onDesktop {
      margin-top: 63px;
  
      & > :nth-child(n + 2) {
        display: block;
        margin-left: 30px;
      }
    }
  }
  
  &__card {
    flex-grow: 1;
  
    &__img {
      width: 100%;
      height: 267px;
  
      object-fit: cover;
      object-position: center;
  
      @include onIPad {
        height: 300px;
      }
  
      @include onDesktop {
        height: 380px;
  
        transition: 0.2s;
  
        &:hover {
          transform: scale(1.05);
        }
      }
    }
  
    &__name {
      margin-top: 15px;
      padding-left: 5px;
      font-size: 20px;
      line-height: 30px;
      letter-spacing: 1px;
      text-transform: uppercase;
  
      @include onIPad {
        margin-top: 20px;
      }
    }
  
    &__price {
      margin-top: 10px;
      padding-left: 5px;
      font-size: 18px;
      line-height: 27px;
      letter-spacing: 1px;
      text-transform: uppercase;
    }
  }
  
  &__button {
    margin: 78px auto 0;
  
    @include onIPad {
      margin-top: 40px;
    }
  }
  
  
}
